/* General Layout */
.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 2em;
  text-align: center;
}

form {
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
}

input,
select {
  margin: 0 10px 10px 0;
  padding: 10px;
  width: calc(50% - 20px);
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease;
}

input:focus,
select:focus {
  border-color: #007bff;
  outline: none;
}

button {
  padding: 10px 15px;
  margin: 10px 5px 10px 0;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  font-size: 16px;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

button:hover {
  background-color: #0056b3;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Primary (e.g., Add Task) and Danger (e.g., Delete) Button Styles */
button.Add Task {
  background-color: #28a745;
}

button.Add Task:hover {
  background-color: #218838;
}

button.Delete {
  background-color: #dc3545;
}

button.Delete:hover {
  background-color: #c82333;
}

/* Task List Styling */
.task-list {
  list-style: none;
  padding: 0;
}

.task-item {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-item span {
  font-weight: bold;
}

.task-actions {
  display: flex;
  gap: 10px;
}

/* Task Status Badges */
.badge {
  display: inline-block;
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 12px;
  color: white;
  margin-left: 10px;
}

.badge.in-progress {
  background-color: #ffc107;
}

.badge.completed {
  background-color: #28a745;
}

.badge.pending {
  background-color: #007bff;
}

/* Notifications */
.notification {
  background-color: #d4edda;
  color: #155724;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 20px;
  text-align: center;
}

.notification.error {
  background-color: #f8d7da;
  color: #721c24;
}

/* Task Filter Inputs */
.task-filters {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.task-filters input {
  width: calc(40% - 10px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease;
}

.task-filters input:focus {
  border-color: #007bff;
}

.task-filters select {
  width: calc(30% - 10px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease;
}

.task-filters select:focus {
  border-color: #007bff;
}

/* Hover & Focus States */
input:focus,
select:focus {
  border-color: #007bff;
  outline: none;
}
